
.food {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 48px;
  z-index: 30px;
  width: 100%;
  background: #fff;
}
.food.move-enter-active, .food.move-leave-active {
  transition: all .2s linear;
}
.food.move-enter, .food.move-leave-active {
  transform: translate3d(100%, 0, 0);
}
.food .image-header {
  position: relative;
  width: 100%;
  height: 0;
  padding-top: 100%;
}
.food .image-header img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.food .image-header .back {
  position: absolute;
  top: 10px;
  left: 0;
}
.food .image-header .icon-arrow_lift {
  display: block;
  padding: 10px;
  font-size: 20px;
  color: #fff;
}
.food .content {
  padding: 18px;
  position: relative;
}
.food .content .title {
  line-height: 14px;
  margin-bottom: 8px;
  font-size: 14px;
  font-weight: 700;
  color: rgb(7, 17, 27);
}
.food .content .detail {
  margin-bottom: 18px;
  line-height: 10px;
  font-size: 0;
  height: 10px;
}
.food .content .detail .sell-count,
.food .content .detail .rating {
  font-size: 10px;
  color: rgb(147, 153, 159);
}
.food .content .detail .sell-count {
  margin-right: 12px;
}
.food .content .price {
  font-weight: 700;
  line-height: 24px;
}
.food .content .price .now {
  margin-right: 8px;
  font-size: 14px;
  color: rgb(240, 20, 20);
}
.food .content .price .old {
  text-decoration: line-through;
  font-size: 10px;
  color: rgb(147, 153, 159);
}
.food-content .cartcontrol-wrapper {
  position: absolute;
  right: 12px;
  bottom: 12px;
}
.food-content .buy {
  position: absolute;
  right: 18px;
  bottom: 18px;
  z-index: 10;
  height: 24px;
  line-height: 24px;
  padding: 0 12px;
  box-sizing: border-box;
  font-size: 10px;
  border-radius: 12px;
  color: #fff;
  background: rgb(0, 160, 220);
}
.food-content .buy.fade-enter-active, .food-content .buy.fade-leave-active {
  transition: all .2s;
}
.food-content .buy.fade-enter, .food-content .buy.fade-leave-active {
  opacity: 0;
}
.food-content .info {
  padding: 18px;
}
.food-content .info .title {
  line-height: 14px;
  margin-bottom: 6px;
  font-size: 14px;
  color: rgb(77, 85, 93);
}
.food-content .info .text {
  line-height: 24px;
  padding: 0 8px;
  font-size: 12px;
  color: rgb(77, 85, 93);
}
.food-content .rating {
  padding-top: 18px;
}
.food-content .rating .title {
  line-height: 14px;
  margin-left: 18px;
  font-size: 14px;
  color: rgb(7, 17, 27);
}
.rating .rating-wrapper {
  padding: 0 18px;
}
.rating .rating-wrapper .rating-item {
  position: relative;
  padding: 16px 0;
  border-bottom: 1px solid rgba(7, 17, 27, .1);
}
.rating .rating-wrapper .rating-item .user {
  position: absolute;
  right: 0;
  top: 16px;
  line-height: 12px;
  font-size: 0;
}
.rating .rating-wrapper .rating-item .user .name {
  display: inline-block;
  vertical-align: top;
  font-size: 10px;
  color: rgb(147, 153, 159);
  margin-right: 6px;
}
.rating .rating-wrapper .rating-item .user .avatar {
  border-radius: 50%;
}
.rating .rating-wrapper .rating-item .time {
  margin-bottom: 6px;
  line-height: 12px;
  font-size: 10px;
  color: rgb(147, 153, 159);
}
.rating .rating-wrapper .rating-item .text {
  line-height: 16px;
  font-size: 12px;
  color: rgb(7, 17, 27);
}
.rating .rating-wrapper .rating-item .text .icon-thumb_up,
.rating .rating-wrapper .rating-item .text .icon-thumb_down {
  margin-right: 4px;
  line-height: 16px;
  font-size: 12px;
}
.rating .rating-wrapper .rating-item .text .icon-thumb_up {
  color: rgb(0, 160, 220);
}
.rating .rating-wrapper .rating-item .text .icon-thumb_down {
  color: rgb(147, 153, 159);
}
.rating .rating-wrapper .no-rating {
  padding: 16px 0;
  font-size: 12px;
  color: rgb(147, 153, 159);
}





