
.ratingselect .rating-type {
  padding: 18px 0;
  margin: 0 18px;
  border-bottom: 1px solid rgba(7, 17, 27, .1);
  font-size: 0;
}
.ratingselect .block {
  display: inline-block;
  padding: 8px 12px;
  margin-right: 8px;
  border-radius: 1px;
  color: rgb(77, 85, 93);
  font-size: 12px;
  line-height: 16px;
}
.ratingselect .block.active {
  color: #fff;
}
.ratingselect .block.positive {
  background: rgba(0, 160, 220, .2);
}
.ratingselect .block.positive.active {
  background: rgb(0, 160, 220);
}
.ratingselect .block.negative {
  background: rgba(77, 85, 93, .2);
}
.ratingselect .block.negative.active {
  background: rgb(77, 85, 93);
}
.ratingselect .block .count {
  font-size: 8px;
  margin-left: 2px;
}
.ratingselect .switch {
  padding: 12px 18px;
  line-height: 24px;
  border-bottom: 1px solid rgba(7, 17, 27, .1);
  color: rgb(147, 153, 159);
  font-size: 0;
}
.ratingselect .switch.on .icon-check_circle {
  color: #00c850;
}
.ratingselect .switch .icon-check_circle {
  display: inline;
  vertical-align: top;
  margin-right: 4px;
  font-size: 24px;
}
.ratingselect .switch .text {
  display: inline-block;
  vertical-align: top;
  font-size: 12px;
}





